@import url("https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@0;1&display=swap");

* {
  font-family: "DM Serif Display", serif;
}
body {
  background-color: #f2f2f2;
  margin: 0;
  box-sizing: border-box;
}

.department-search-model {
  margin-top: 90px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #242424;
}

.departments-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.department-buttons-inputs {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.departments-content {
  list-style: none;
  background-color: #d6e6f2;
  border-radius: 5px;
  width: 300px;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.members-content {
  padding: 5px;
  letter-spacing: 1px;
  color: #4e709d;
}

.delete-btn {
  width: 300px;
  height: 30px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  background-color: #4e709d;
  color: #d6e6f2;
  transition: 1s;
  margin-bottom: 20px;
}

.editData-btn {
  width: 300px;
  height: 30px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  background-color: #4e709d;
  color: #d6e6f2;
  transition: 1s;
  margin: 20px 0;
}
